import 'antd/dist/antd.css';
import React, { useEffect, useState } from 'react';
import { Button, Layout, Menu, Table } from 'antd';
import { EditTwoTone } from '@ant-design/icons';
import Form from './RatingForm.jsx';
const { Header, Content } = Layout;

const toSentenceCase = str => {
  const spaced = str.replace(/([A-Z])/g,' $1');
  return spaced.charAt(0).toUpperCase() + spaced.slice(1);
};

const App = () => {
  const [userData, setUserData] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedUser, setSelectedUser] = useState(null);

  const button = (user) => (
    <Button
    type="primary"
    shape="circle"
    icon={<EditTwoTone />}
    onClick={() => {
      setShowModal(true);
      setSelectedUser(user);
    }}
    />
  );

  const transformDataByRank = (data) => {
    data.sort((first, second) => second.score - first.score);
    const displayData = data.map(({imgUrl, ...rest}) => ({...rest}));
    return displayData.map((user, index) => ({
      rank: index + 1 , ...user, actions: button(user)
    }));
  };

  const fetchData = async() => {
    const raw = await fetch('/users');
    const data = await raw.json();
    setUserData(transformDataByRank(data));
  };

  useEffect(() =>  {
    fetchData();
  }, []);

  const tableColumns = userData && userData.length && [
     ...Object.keys(userData[0]).filter(
       (col) => !['imgUrl', 'id'].includes(col)).map((col, index) =>({
        key: index,
        dataIndex: col,
        title: toSentenceCase(col),
       })),
  ];

  const onSubmit = async (increment) => {
    await fetch(`/users/${selectedUser.id}`, {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(increment),
    });
    setShowModal(false);
    setSelectedUser(null);
    fetchData();
  }

  return (
   <Layout>
      <Content>
        <Table columns={tableColumns} dataSource={userData} />
        <Form onSubmit={onSubmit} visible={showModal} onCancel={() => setShowModal(false)} />
      </Content>
   </Layout>
  );
}

export default App;
