import './Form.css';

import React, { Fragment, useEffect, useState } from 'react';
import { Form, Rate, Typography, Modal} from 'antd';
const { Title, Text } = Typography;

export default ({ visible, onCancel, onSubmit }) => {
  const [ratings, setRatings] = useState([]);
  const [value, setValue] = useState(null);
  const [form] = Form.useForm();

  useEffect(() => {
    (async() => {
      const raw = await fetch('/ratings');
      const data = await raw.json();
      setRatings(data);
    })()
  }, []);

  const onChange = value => setValue(value);

  return (
    <Modal
    title="Rate the latest bad joke"
    visible={visible}
    okText="Submit"
    onCancel={onCancel}
    onOk={() => {
      form
      .validateFields()
      .then(values => {
        onSubmit(values);
        onCancel();
      })
      .catch(info => {
        console.log('Validate Failed:', info);
      });
    }}
    >
      <Form
        form={form}
        layout="vertical"
        name="rating-modal"
      >
        <Form.Item
          name="rating"
          label="Rating"
          rules={[
            {
              required: true,
              message: 'You have to select rating before you can submit.',
            },
          ]}
        >
          <Rate
            count={ratings.length}
            tooltips={ratings.map(rating => (
              <Fragment>
                <h3>{rating.title}</h3>
                <p>{rating.description}</p>
              </Fragment>
            ))}
            value={value}
            onChange={() => {
              onChange(value);
            }
            }
            allowClear
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};